@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
.container {
  background: #E5E5E5;
  width: 100%;
  min-height: 100vh;
}
.container .first-section {
  background: #E5E5E5;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 60px;
  position: absolute;
  top: 96px;
}
@media (max-width: 1024px) {
  .container .first-section {
    flex-direction: column;
  }
}
@media (max-width: 540px) {
  .container .first-section {
    padding: 20px;
  }
}
.container .first-section .first-section__img {
  width: 505px;
  height: 303px;
}
@media (max-width: 540px) {
  .container .first-section .first-section__img {
    width: 100%;
    margin: 20px;
  }
}
.container .first-section .first-section__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 40px;
}
@media (max-width: 1024px) {
  .container .first-section .first-section__content {
    align-items: center;
  }
}
.container .first-section .first-section__content .first-section__heading {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 50.4px;
  color: #121212;
}
.container .first-section .first-section__content .first-section__description {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  max-width: 380px;
}
@media (max-width: 1024px) {
  .container .first-section .first-section__content .first-section__description {
    text-align: justify;
  }
}
@media (max-width: 540px) {
  .container .first-section .first-section__content .first-section__description {
    padding: 15px;
  }
}
.container .first-section .first-section__content .first-section__btn {
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: #FFFFFF;
  background-color: #E4C1AE;
  padding: 16px 40px;
  border-radius: 32px;
  border: none;
  cursor: pointer;
}

.modal .modal__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(18, 18, 18, 0.5);
  z-index: 99;
}
.modal .modal__close {
  position: absolute;
  top: 40px;
  right: 40px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.modal .modal__content {
  background: #FFFFFF;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  min-height: 200px;
  width: 60%;
  padding-left: 60px;
}
.modal .modal__content .modal__heading {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 50.4px;
}
.modal .modal__content .modal__description {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
}

*, *::before, *::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}